import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import { parsePhoneNumberFromString, CountryCode } from 'libphonenumber-js';
import PhoneInput, { CountryData } from 'react-phone-input-2';
import 'react-phone-input-2/lib/style.css';

import { Box } from '@mui/material';
import TextField from "@mui/material/TextField";
import { styled } from "@mui/system";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSyncAlt } from "@fortawesome/free-solid-svg-icons";

import PicAuthCode from "../../../components/PicAuthCode";
import BackBar from "../../../components/BackBar";
import TextInput from "../../../components/TextInput";
import Button from "../../../components/Button";

const CustomTextField = styled(TextField)({
  "& .MuiInputBase-input": {
    padding: "6px", // 調整 padding
    color: "#333", // 字體顏色
    backgroundColor: "#f5f5f5", // 背景色
    borderRadius: "4px", // 圓角
  },
  "& .MuiFormControl-root": {
    width: "100%", // 寬度設置 100%
  },
});

const MobileVerification: React.FC = () => {
  const navigate = useNavigate();
  const { t } = useTranslation();
  const [phoneNumber, setPhoneNumber] = useState<string>("+886"); // 預設值為 +886
  const [country, setCountry] = useState<CountryCode>('TW');
  const [receivedCode, setReceivedCode] = useState<string>("");
  const [enterCode, setEnterCode] = useState<string>("");
  const [smsCode, setSmsCode] = useState<string>("");
  const [smsVerifySuccess, setSmsVerifySuccess] = useState(true);
  const testSmsCode = '123';
  const [errors, setErrors] = useState({
    phone: "",
    enterCode: "",
    smsCode: ""
  });

  const validateInputs = () => {
    let isValid = true;
    const newErrors = {
      phone: "",
      enterCode: "",
      smsCode: ""
    };
    console.log(country);

    try {
      const phoneNumberParsed = parsePhoneNumberFromString(phoneNumber, country);
      if (phoneNumberParsed && phoneNumberParsed.isValid()) {
        isValid= true;
      } else {
        isValid = false;
        newErrors.phone = '手機號碼格式錯誤，請重新輸入';
      }
    } catch (error) {
      isValid = false;
      newErrors.phone = '手機號碼格式錯誤，請重新輸入';
    }

    if (receivedCode !== enterCode) {
      isValid = false;
      newErrors.enterCode = '圖形驗證碼填寫錯誤，請重新輸入';
    } 

    console.log(isValid);

    setErrors(newErrors);
    return isValid;
  };

  const handleCodeChange = (code: string) => {
    setReceivedCode(code); // 將驗證碼存入狀態中
  };

  const handlePhoneChange = (phoneValue: string, countryData: CountryData) => {
    setPhoneNumber(phoneValue);
    setCountry(countryData.countryCode.toUpperCase() as CountryCode); // 設置選擇的國家名稱
    console.log(countryData);
  };

  const [isSmsVerificationVisible, setIsSmsVerificationVisible] =
    useState<boolean>(false); // 控制是否顯示簡訊驗證
  const [isVerificationSuccessful, setIsVerificationSuccessful] =
    useState<boolean>(false); // 控制驗證成功顯示

  const handleCaptchaSubmit = () => {
    // 處理圖形驗證碼提交的邏輯
    // 成功後顯示簡訊驗證
    if (validateInputs())
    setIsSmsVerificationVisible(true);
  };

  const handleSmsVerification = () => {
    // 處理簡訊驗證碼的提交邏輯
    if (smsCode === testSmsCode) {
      setSmsVerifySuccess(true);
      setIsVerificationSuccessful(true);
    } else {
      setSmsVerifySuccess(false);
      setIsVerificationSuccessful(false);
    }
  };

  return (
    <div>
      <BackBar customTitle={t("簡訊驗證")} />
      <div className="p-4">
        {!isSmsVerificationVisible && !isVerificationSuccessful && (
          <form className="space-y-4">
            <div>
              <label className="block mb-2">{t("手機號碼")}</label>
              <PhoneInput
                country={'tw'} // 設定預設國家
                value={phoneNumber} // 綁定電話號碼的狀態
                onChange={handlePhoneChange} // 當電話號碼變動時更新狀態
                dropdownStyle={{
                  color: 'black', // 修改背景色
                  border: '1px solid #333', // 修改邊框
                  borderRadius: '8px', // 圓角
                }}
                inputStyle={{color: 'black', width: '100%'}}
              
                containerStyle={
                  errors.phone !== '' 
                  ? {border: '2px solid red', borderRadius: '4px'}
                  : {border: '1px solid #555555', borderRadius: '4px'}     
                }
              />
              {errors.phone && <p className="text-red-500 text-xs">{errors.phone}</p>}
            </div>
            <div>
              <label className="block mb-2">{t("圖形驗證")}</label>
              <TextInput
                type="text"
                placeholder={t("圖形驗證碼")}
                className={errors.enterCode != '' ? "border-red-500" : ''}
                onInputChange={(value: string)=>setEnterCode(value)}
                value={enterCode}
              />
              {errors.enterCode && <p className="text-red-500 text-xs">{errors.enterCode}</p>}
            </div>
            <div className="flex items-center mt-2">
              <PicAuthCode onCodeChange={handleCodeChange} />
              <button
                type="button"
                onClick={() => handleCodeChange("")}
                className="ml-5"
                aria-label="重新整理驗證碼"
              >
                <FontAwesomeIcon icon={faSyncAlt} />
              </button>
            </div>
            <div>
              <Button
                className="button-full active mt-2"
                type="button"
                onClick={handleCaptchaSubmit} // 簡訊驗證的處理
              >
                {t("送出")}
              </Button>
            </div>
          </form>
        )}
        {/* 簡訊驗證區塊 */}
        {isSmsVerificationVisible && !isVerificationSuccessful && (
          <form className="space-y-4">
            <div className="mt-4">
              <label className="block mb-2">簡訊驗證碼</label>
              <TextInput
                placeholder="請輸入簡訊驗證碼"
                value={smsCode}
                onInputChange={(value: any) =>
                  setSmsCode(value)
                }
                className={ !smsVerifySuccess ? 'border-red-500' : '' }
                autocomplete="username"
              />
              { !smsVerifySuccess && <p className="text-red-500 text-xs">簡訊驗證碼填寫錯誤，請重新輸入</p> }
              <div className="mt-4">
                <Button
                  className="button-full active mt-2"
                  type="button"
                  onClick={handleSmsVerification}
                >
                  {t("送出")}
                </Button>
                <Button
                  className="button-full my-3 border border-secondary text-secondary"
                  type="button"
                  onClick={()=>navigate(-1)}
                >
                  {t("修改手機號碼")}
                </Button>
              </div>
              <p className="text-xs text-center ">
                {t("沒收到驗證簡訊? 聯絡客服")}
              </p>
            </div>
          </form>
        )}

        {/* 驗證成功的訊息 */}
        {isVerificationSuccessful && (
          <div className="mt-4 text-center flex flex-col items-center">
            <img
              src={require("../CheckmarkCircle.png")}
              className="m-4 w-20 h-20 "
              alt="Verification Success"
            />
            <p className="font-bold text-lg">
              {t("恭喜帳號驗證成功，")}
              <br />
              {t("免費館馬上看、85折限時折價券")}
            </p>
            <Button className="button-full active mt-2" type="button">
              {t("立即使用")}
            </Button>
          </div>
        )}
      </div>
    </div>
  );
};

export default MobileVerification;
